
import progress from '../common/progress.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
components: {
ViewBox,
XHeader,
Group,
Masker,
XInput,
Radio,
PopupPicker,
'rate': progress
},
data () {
return {
index: 0,
isAddStatus: '1',
radio: [],
newRate: '',
isConfirmShow: false,
isSignTipShow: false,
couponId: '', // id
isSelectCard: false,
addAssurePeople: false,
showBindCard: false,
bindCardNo: '', // 
hasBindCardNo: '', // 
initAssureName: '',
initAssurePhone: '',
msg: '', // 
timeLimit: '', // 
lineCredit: '', // 
applyRate: '', // 
couponList: [], // 
couponNum: 0,
couponAmount: [''],
coupon: '',
bankcardList: [], // 
assureName: '', // 
assurePhone: '', // 
getAssurePhone: '', // 
getAssureName: '', // 
getAssureIdCard: '', // 
assureIdcard: '' // 
}
},
mounted () {
this.initPage()
},
computed: {
step () {
return this.$store.state.applyState
},
isHandlerAssure () {
return this.$store.state.applyState === 3
}
},
methods: {
nextTimeSign () {
this.$router.replace('/checkLoan')
this.$store.state.tabItem = 1
},
knowTip () {
this.isSignTipShow = false
this.$router.replace('/checkLoan')
},
cancelBindBankCard () {

this.showBindCard = false
},
confirm () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/addOrUpdateAssure.do'
let data = {
action: 'init_request',
path: path,
params: {
token: self.$store.state.userInfo.token,
assureName: self.assureName,
assurePhone: self.assurePhone,
applyId: self.$store.state.applyNo,
type: '1' // 1 2 
}
}
if (self.assureName !== '') {
if (self.assurePhone !== '') {
self.$store.dispatch('normalRequest', data).then(data => {
self.addAssurePeople = false
if (data.response === 'success') {
self.assureName = ''
self.assurePhone = ''
self.updateAddAssureStatus()
} else {
self.isAddStatus === '1' ? this.$vux.toast.text('') : this.$vux.toast.text('')
}
})
} else {
this.$vux.toast.text('')
}
} else {
this.$vux.toast.text('')
}
},
toVertify () {
this.showBindCard = false
this.$router.push('/addCard')
},
giveUpSign () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/giveUpSignContract.do'
let data = {
action: 'jump_web_refuse',
path: path,
params: {
token: self.$store.state.userInfo.token,
applyId: self.$route.query.applyNo // 
}
}
self.isConfirmShow = false

self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
this.$vux.toast.text('')
this.$router.replace('/checkLoan')
this.$store.state.tabItem = 1
} else {
this.$vux.toast.text(res.data)
}
})
},
couponChange (value) {
if (value) {
this.couponId = value[0]
this.getNewRate(value)
}
},
chooseBankCard () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/chooseBankCard.do'
let data = {
action: 'init_request',
path: path,
params: {
token: self.$store.state.userInfo.token,
applyId: self.$store.state.applyNo,
bankCardNo: self.bindCardNo
}
}
if (self.bindCardNo !== '') {
self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
self.hasBindCardNo = self.bindCardNo
this.$vux.toast.text('')
} else {
this.$vux.toast.text('')
}
})
} else {
this.$vux.toast.text('')
}
this.showBindCard = false
},
getNewRate (value) {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/computeRateByCoupon.do'
let data = {
action: 'init_request',
path: path,
params: {
userToken: self.$store.state.userInfo.token,
apprAmount: self.lineCredit, // 
apprRate: self.applyRate, // 
apprTerm: self.timeLimit, // 
couponCode: value[0] // id
}
}
self.$store.dispatch('normalRequest', data).then(data => {

if (data.response === 'success') {
self.newRate = data.data
}
})
},
sign () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/contract.html?userToken=' + self.$store.state.userInfo.token + '&applyId=' + self.$store.state.applyNo
let data = {
action: 'jump_web_sign',
path: path,
params: {
token: self.$store.state.userInfo.token,
applyId: self.$store.state.applyNo,
type: '1', // 1 2
couponId: self.couponId,
newRate: self.newRate,
cardNo: self.bindCardNo,
url: self.$store.state.baseUrl + '/app/xsyd/signContract.do',
name: ''
}
}
if (data.params.cardNo !== '') {
self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
this.isSignTipShow = true


}
})
} else {
this.$vux.toast.text('')
}
},
bindCard () {
self.showBindCard = false
},
addAssure () {
this.isAddStatus = '1' // 
this.addAssurePeople = true
},
changeAssure () {
this.isAddStatus = '2' // 
this.addAssurePeople = true
},
getProgress (type) {
let self = this
switch (type) {
case '1': self.$store.state.applyState = 1; self.page = '/quotaEvaluation' // 
break
case '2': self.$store.state.applyState = 2; self.page = '/fail' // 
break
case '3': self.$store.state.applyState = 3; self.page = '/sign' // 
break
case '4': self.$store.state.applyState = 4; self.page = '/sign' // 
break
case '5': self.$store.state.applyState = 5; self.page = '/success' // 
break
case '6': self.$store.state.applyState = 6; self.page = '/fail' // 
break
}
},
updateAddAssureStatus () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/applyPageInit.do'
let data = {
action: 'init_request',
path: path,
params: {
token: this.$store.state.userInfo.token
}
}
self.$store.dispatch('normalRequest', data).then(data => {

if (data.response === 'success' && data.data.canApply !== 1) {
self.getProgress(data.data.explain)
} else {
this.$vux.toast.text(data.data)
}
})
},
revokeAssure () {

let self = this

let path = self.$store.state.baseUrl + '/app/xsyd/revokeAssure.do'
let data = {
action: 'init_request',
path: path,
params: {
token: self.$store.state.userInfo.token,
applyId: self.$store.state.applyNo
}
}
this.$vux.loading.show({
text: 'Loading'
})
self.$store.dispatch('normalRequest', data).then(data => {

if (data.response === 'success') {
self.updateAddAssureStatus()
self.initPage()
} else {
this.$vux.toast.text(data.data)
}
this.$vux.loading.hide()
})
},
initPage () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/applicantSignPageInit.do'
let data = {
action: 'init_request',
path: path,
params: {
token: this.$store.state.userInfo.token,
applyId: this.$store.state.applyNo
}
}

self.$store.dispatch('normalRequest', data).then(data => {
if (data.response === 'success') {
self.msg = data.data.message
self.timeLimit = data.data.timeLimit // 
self.lineCredit = data.data.lineCredit // 
self.applyRate = data.data.loanInterestRate // 
self.getAssurePhone = data.data.assurePhone
self.getAssureName = data.data.assureName
self.getAssureIdCard = data.data.assureIdcard
self.initAssureName = data.data.assureName
data.data.couponList.forEach(function (element) {
self.couponList.push({ name: element.couponAmount, value: element.couponCode, parent: 0 })
})
data.data.bankcardList.forEach(function (element) {
self.radio.push({ key: element.bankcardNo, value: element.bankcardNo })
})
} else {
this.$vux.toast.text(data.data)
}
})
},
jumpAgree () {
let self = this
let token = self.$store.state.userInfo.token
let path = self.$store.state.baseUrl + '/app/xsyd/contactUs.html?' + token
let data = {
action: 'jump_web_sign',
path: path,
params: {
token: this.$store.state.userInfo.token,
usedFor: this.value1[0],
lowAddress: this.value2[0],
zmxyFlag: '1',
url: self.$store.state.baseUrl + '/app/xsyd/submitApply.do',
name: ''
}
}
if (data.params.usedFor !== '') {
if (data.params.lowAddress !== '') {
self.$store.dispatch('normalRequest', data).then(data => {
if (data.response === 'success') {
this.$vux.toast.text('')
this.$router.replace('/quotaEvaluation')
} else {
this.$vux.toast.text(data.data)
}
})
} else {
this.$vux.toast.text('')
}
} else {
this.$vux.toast.text('')
}
}
}
}



